* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "Archivo", sans-serif;
  color: #222;
}

input {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "Archivo", sans-serif;
  color: #222;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

h3 {
  font-weight: 500;
}

h4 {
  font-weight: 500;
}

.header {
  position: relative;
}

.container {
  max-width: 117rem;
  margin: 0 auto;
  padding: 0 1.6rem;
}

.navigation {
  position: relative;
  height: 7rem;
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  height: 7rem;
  padding: 0 1rem;
}

.fix__nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1200;
}

.nav__logo a {
  font-size: 2.5rem;
  color: #222;
  padding: 1.6rem;
  font-weight: 700;
}

.nav__hamburger {
  display: none;
  cursor: pointer;
}
.nav__hamburger svg {
  height: 2.3rem;
  width: 2.3rem;
}

.menu__top {
  display: none;
}

.nav__menu {
  width: 50%;
}

.nav__list {
  display: flex;
  align-items: center;
  height: 100%;
  width: 50%;
}
.nav__list .nav__link:link {
  display: inline-block;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #222;
  transition: color 0.3s ease-in-out;
}
.nav__list .nav__link:visited {
  display: inline-block;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #222;
  transition: color 0.3s ease-in-out;
}
.nav__list .nav__link:hover {
  color: #eb0028;
}

.nav__item:not(:last-child) {
  margin-right: 1.6rem;
}

.nav__icons {
  display: flex;
  position: relative;
}
.nav__icons .icon__item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.7rem;
  border: 1px solid #222;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out;
}
.nav__icons .icon__item svg {
  width: 1.6rem;
  height: 1.6rem;
}
.nav__icons .icon__item:link {
  color: #222;
}
.nav__icons .icon__item:visited {
  color: #222;
}
.nav__icons .icon__item:hover {
  background-color: #eb0028;
  border: 1px solid #222;
}
.nav__icons .icon__item:not(:last-child) {
  margin-right: 1rem;
}
.nav__icons #cart__total {
  font-size: 1rem;
  position: absolute;
  top: 2px;
  right: -6px;
  background-color: #eb0028;
  padding: 0.2rem 0.4rem;
  border-radius: 100%;
  color: #f1f1f1;
}

.page__title-area {
  background-color: #f1f1f1;
}

.page__title-container {
  padding: 1rem;
}

.page__titles {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  color: #666;
}
.page__titles a {
  margin-right: 2rem;
}
.page__titles a svg {
  width: 1.8rem;
  height: 1.8rem;
  fill: #666;
}

.page__title {
  position: relative;
}
.page__title::before {
  position: absolute;
  content: "/";
  top: 0;
  left: -1rem;
}

@media only screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: 0;
    left: -30rem;
    width: 0;
    background-color: #fff;
    z-index: 9990;
    height: 100%;
    transition: all 0.5s;
  }
  .nav__menu.open {
    left: 30rem;
    width: 30rem;
  }
  .nav__logo {
    width: 50%;
  }
  .nav__hamburger {
    display: block;
  }
  .menu__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #eb0028;
    padding: 1.8rem 1rem;
  }
  .menu__top svg {
    height: 1.6rem;
    width: 1.6rem;
    fill: #f1f1f1;
  }
  .nav__category {
    color: #fff;
    font-size: 2.3rem;
    font-weight: 700;
  }
  .nav__list {
    flex-direction: column;
    align-items: start;
    padding: 1.6rem 1rem;
  }
  .nav__list .nav__link:link {
    padding: 1.6rem 0;
    width: 100%;
    color: #666;
  }
  .nav__list .nav__link:visited {
    padding: 1.6rem 0;
    width: 100%;
    color: #666;
  }
  .nav__item {
    width: 100%;
  }
  .nav__item:not(:last-child) {
    margin-right: 0;
  }
  body.active::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 110%;
    background: #222 none no-repeat 0 0;
    opacity: 0.7;
    z-index: 999;
    transition: 0.8s;
  }
}
@media only screen and (max-width: 568px) {
  .nav__icons .icon__item {
    padding: 0.4rem;
  }
  .nav__icons .icon__item svg {
    width: 1.4rem;
    height: 1.4rem;
  }
}
.hero {
  background-color: var(--primaryColor);
  position: relative;
  width: 100%;
  height: 100vh;
}
.hero .glide__slides {
  background-color: var(--primaryColor);
  position: relative;
  width: 100%;
  height: 100vh;
}
.hero .glide__bullet {
  background-color: #222;
  width: 1.2rem;
  height: 1.2rem;
}
.hero .glide__arrow {
  padding: 1.5rem 1.7rem;
  opacity: 0;
  border: none;
  background-color: var(--grey);
  transition: all 0.5s ease-in-out 0.2s;
  border-radius: 50%;
}
.hero .glide__arrow svg {
  height: 1.8rem;
  width: 1.8rem;
  fill: var(--primaryColor);
}
.hero:hover .glide__arrow {
  opacity: 1;
}
.hero:hover .glide__arrow--left {
  left: 12rem;
}
.hero:hover .glide__arrow--right {
  right: 23rem;
}

.glide__arrow:hover {
  background-color: var(--black);
}

.glide__arrow--left {
  left: 20rem;
}

.glide__arrow--right {
  position: absolute;
  right: 20rem;
}

.hero__center {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 114rem;
  margin: 0 auto;
  height: 100%;
  padding-top: 3rem;
}

.hero__left {
  padding: 0 3rem;
  flex: 0 0 50%;
}
.hero__left .hero__btn:hover {
  font-weight: 700;
  background-color: transparent;
  color: var(--black);
}
.hero__left h1 {
  margin: 1rem 0;
}
.hero__left p {
  margin-bottom: 1rem;
}

.hero__btn {
  display: inline-block;
  font-weight: 700;
  font-size: 1.4rem;
  background-color: var(--black);
  color: var(--primaryColor);
  cursor: pointer;
  margin-top: 1rem;
  padding: 1.5rem 4rem;
  border: 1px solid var(--black);
  transition: all 0.3s ease-in-out;
}
.hero__btn:focus {
  outline: none;
}

.hero__right {
  flex: 0 0 50%;
  position: relative;
  text-align: center;
}
.hero__right img.banner_03 {
  width: 70%;
}

@media only screen and (max-width: 999px) {
  .hero__center {
    flex-direction: column;
    text-align: center;
  }
  .hero__right {
    top: 50%;
    position: absolute;
  }
  .hero__right img {
    width: 55%;
  }
  .hero__left {
    position: absolute;
    padding: 0 1rem;
    top: 20%;
  }
  .hero__btn {
    padding: 1.1rem 2.8rem;
  }
  .hero .glide__arrows {
    display: none;
  }
}
@media only screen and (max-width: 567px) {
  .hero {
    height: 60vh;
  }
  .hero .glide__slides {
    height: 60vh;
  }
}
/*
======================
Latest Products
======================
*/
.title__container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 6rem;
  padding: 2rem 0;
  background-color: #f1f1f1;
  position: relative;
  top: 20px;
}
.title__container a {
  color: #000;
}

a.go-to-products {
  display: flex;
  justify-content: flex-end;
}
a.go-to-products svg {
  width: 16px;
  margin-left: 10px;
}

img.product-img {
  max-height: 210px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

/* Shop Content */
.shop-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, auto));
  gap: 1.5rem;
}

.product-box:hover {
  padding: 10px;
  border: 1px solid var(--text-color);
  transition: 0.4s;
}

.product-title {
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}

.footer {
  background-color: #252525;
  padding: 6rem 1rem;
  line-height: 3rem;
  margin-top: 20px;
}

.footer-top__box span {
  margin-right: 1rem;
}
.footer-top__box span svg {
  width: 1.6rem;
  height: 1.6rem;
  fill: #ccc;
}
.footer-top__box a:link {
  display: block;
  color: #959595;
  font-size: 1.4rem;
  transition: 0.6s;
}
.footer-top__box a:visited {
  display: block;
  color: #959595;
  font-size: 1.4rem;
  transition: 0.6s;
}
.footer-top__box a:hover {
  color: #eb0028;
}
.footer-top__box div {
  color: #959595;
  font-size: 1.4rem;
}
.footer-top__box h3 {
  font-size: 1.8rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

.footer__top {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  color: #ccc;
}

@media only screen and (max-width: 998px) {
  .footer__top {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}
@media only screen and (max-width: 768px) {
  .footer__top {
    grid-template-columns: 1fr;
    row-gap: 2rem;
  }
}
.cart-list {
  position: absolute;
  right: -17px;
  top: 38px;
  z-index: 13;
  display: none;
}

.cart-list.cart-list-view {
  display: block;
}

.glide__arrows {
  position: relative;
  top: -67px;
}

img.cart-item__image {
  max-height: 100px;
}

.cart-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
  margin: 8px;
  padding-bottom: 11px;
}

.img-div {
  margin-right: 11px;
}

input[type=number] {
  width: 44px;
}

button.cart-item__remove-btn {
  float: right;
  margin-right: 10px;
  width: 16px;
  border: none;
  background: none;
}
button.cart-item__remove-btn svg {
  fill: red;
}

.total {
  /* margin-left: auto; */
  text-align: end;
  margin-right: 10px;
  /* float: right; */
  display: block;
}

div#cart-list {
  width: 325px;
  background: #f9f8f8;
  border-radius: 12px;
  box-shadow: 7px 7px 10px 2px rgba(128, 128, 128, 0.6509803922);
}

a.go-to-cart {
  text-align: right;
  width: 100%;
  display: block;
  padding: 10px;
}
a.go-to-cart svg {
  width: 11px;
}

button.cart-item__update-btn {
  width: 17px;
  border: none;
  background: none;
}
button.cart-item__update-btn svg {
  fill: blue;
}

/*map*/
.iframe {
  margin-left: 25px;
}

@media only screen and (max-width: 998px) {
  .iframe {
    margin-left: 1050px;
    position: static;
    margin-left: 20px;
    margin-top: 20px;
    width: 300px;
    height: 300px;
  }
}